html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.App {
    width: 100vw;
    line-height: 1.15rem;
    background-color: #f6f6ef;
}

@media only screen and (min-width: 750px) {
    .App {
        margin: 0.5rem auto;
    }
}

.App__main {
    margin-left: 0.1rem;
    padding: 0.75rem;
    padding-right: 1.5rem;
}

@media only screen and (min-width: 750px) {
    .App {
        width: 83vw;
    }

    .App__main {
        padding-right: 0.75rem;
    }
}

.Navbar {
    background-color: #ff6600;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 1.1rem;
    padding: 0.2rem 0.9rem;
}

.Navbar__logo {
    font-weight: bold;
    min-width: 6.5rem;
}

.Navbar__itemLink {
    text-decoration: none;
    color: black;
}

.Navbar__menuItem--active {
    text-decoration: none;
    color: white;
}

.Navbar__menuList {
    text-align: left;
}

.Navbar__logo {
    -webkit-flex-basis: 10%;
            flex-basis: 10%;
}

.Navbar__menuList {
    -webkit-flex-basis: 80%;
            flex-basis: 80%;
}

.Navbar__authBtn {
    -webkit-flex-basis: 10%;
            flex-basis: 10%;
}

@media only screen and (min-width: 750px) {
    .Navbar {
        -webkit-flex-direction: row;
                flex-direction: row;
    }

    .Navbar__authBtn {
        text-align: right;
    }
}

@media only screen and (min-width: 750px) {
    .Navbar__menuItem:first-child {
        margin-left: 1rem;
    }
}

.Navbar__menuItem {
    list-style-type: none;
    display: inline-block;
    margin-right: 1rem;
}

.Story {
    margin: 0.5rem 0;
    position: relative;
    bottom: 0.5rem;
}

.StoryHeading__itemLink {
    text-decoration: none;
    color: grey;
    font-size: 0.85rem;
}

.StoryHeading__title {
    font-size: 0.95rem;
    color: black;
}

.StoryHeading__urlLink:hover {
    text-decoration: underline;
}

.StoryMeta__itemLink {
    text-decoration: none;
    color: grey;
}

.StoryMeta__itemLink:hover {
    text-decoration: underline;
}

.StoryMeta {
    color: grey;
    font-size: 0.85rem;
}

.StoryText {
    margin-top: 1rem;
    margin-bottom: 2rem;
    color: grey;
}

.CommentList__nestedComments {
    margin-left: 1rem;
}

@media only screen and (min-width: 750px) {
    .CommentList__nestedComments {
        margin-left: 2rem;
    }
}

.Comment{
    margin-bottom: 1rem;
}
.CommentMeta__itemLink {
    margin-right: 0.25rem;
    text-decoration: none;
    color: grey;
}

.CommentMeta__itemLink:hover {
    text-decoration: underline;
}

.CommentMeta {
    color: grey;
    font-size: 0.85rem;
    margin-bottom: 0.2rem;
}

.CommentText {
    font-size: 0.9rem;
}

.UserDetails__section {
    color: grey;
}

.UserDetails__itemLink {
    color: black;
}

.UserDetails__username,
.UserDetails__accountCreationDate,
.UserDetails__karma,
.UserDetails__about {
